<template>
  <div class="container">
    <div class="main">
      <div class="row">
        <div class="col-md-8 mt-1" style="margin: 0 auto">
          <div class="card mb-3 content">
            <h1 class="m-3 pt-3">{{ $t("bookInfo") }}</h1>
            <div class="card-body">
              <div class="row">
                <div class="col-md-3">
                  <h5>{{ $t("bookTitle") }}</h5>
                </div>
                <div class="col-md-9 text-secondary">
                  <input type="text" v-model="bookObject.Name" />
                </div>
              </div>
              <hr />
              <div class="row">
                <div class="col-md-3">
                  <h5>{{ $t("author") }}</h5>
                </div>
                <div class="col-md-9 text-secondary">
                  <input type="text" v-model="bookObject.Author" />
                </div>
              </div>
              <hr />
              <div class="row">
                <div class="col-md-3">
                  <h5>{{ $t("ISBN") }}</h5>
                </div>
                <div class="col-md-9 text-secondary">
                  <input type="text" v-model="bookObject.ISBN" />
                </div>
              </div>
              <hr />
              <div class="row">
                <div class="col-md-3">
                  <h5>{{ $t("release") }}</h5>
                </div>
                <div class="col-md-9 text-secondary">
                  <input type="date" v-model="bookObject.Release" />
                </div>
              </div>
              <hr />
              <div class="row">
                <div class="col-md-3">
                  <h5>{{ $t("publishingHouse") }}</h5>
                </div>
                <div class="col-md-9 text-secondary">
                  <input type="text" v-model="bookObject.PublishingHouse" />
                </div>
              </div>
              <hr />
              <div class="row">
                <div class="col-md-3">
                  <h5>{{ $t("price") }}</h5>
                </div>
                <div class="col-md-9 text-secondary">
                  <input type="text" v-model="bookObject.price" />
                </div>
              </div>
              <hr />
              <div class="row">
                <div class="col-md-3">
                  <h5>{{ $t("condition") }}</h5>
                </div>
                <div class="col-md-9 text-secondary">
                  <select v-model="bookObject.condition">
                    <option value="condition_1">{{ $t("condition_1") }}</option>
                    <option value="condition_2">{{ $t("condition_2") }}</option>
                    <option value="condition_3">{{ $t("condition_3") }}</option>
                    <option value="condition_4">{{ $t("condition_4") }}</option>
                  </select>
                </div>
              </div>
              <div class="row" style="margin-top: 25px">
                <div class="col-md-4"></div>
                <div class="col-md-6">
                  <button @click.prevent="postBook()" type="button" class="btn btn-info">
                    {{ $t("continue") }}
                  </button>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import UsersService from "../services/UsersService";
import BooksService from "../services/BooksService";

export default {
  name: "NewBook",
  data() {
    return {
      bookObject: {},
    };
  },
  methods: {
    async postBook() {
      this.bookObject.sold = false;
      this.bookObject.Accounts_ID = UsersService.getUserIDFromLocalStorage();

      let result = await BooksService.postNewBook(this.bookObject);
      this.$router.push(`/BookImage/${result.responseObject.insertedId}`);
    },
  },
};
</script>
